<template>
  <div id="modalManageView" role="dialog" class="modal my-popups right fade" v-cloak>
    <div role="document" class="modal-dialog mw-1100">
      <div class="modal-content">
        <div class="modal-header mheader-user">
          <button type="button" class="close" v-on:click="close()">
            <em aria-hidden="true" class="fa fa-times"></em>
          </button>
          <h4 id="myModalLabel" class="modal-title">{{ $t("ManageView") }}</h4>
          <span class="user-guide">
            <a class="ancuserguide" v-if="!ShowLisiting" data-toggle="collapse" href="javascript:void(0)" role="button"
              aria-expanded="false" aria-controls="collapseExample" @click="ShowUserGuide()">User Guide</a>
            <div id="collapseReplyUG" :class="'divancuserguide collapse' + toggleUserGuide">
              <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                <div class="con" v-html="$t('MangeViewUG1')"></div>
              </div>
            </div>
          </span>
        </div>
        <div class="modal-body">
          <loader :is-visible="isLoading"></loader>
          <div class="w-100 float-left" v-if="ShowLisiting">
            <div class="heading-border">
              <h5 class="h5">
                <span>{{ $t("ManageView") }}</span>
                <small class="float-right">
                  <a href="javascript:;" v-on:click="AddNewView()" :title="$t('AddNew')">
                    <em class="fa fa-plus pr-1"></em>
                    {{ $t("AddNew") }}
                  </a>
                  <a href="javascript:;" v-on:click="ClearView()" :title="$t('Clear')"
                    v-bind:class="{ 'disabled': isClearViewDisabled }">
                    <em class="fa fa-refresh pr-1 ml-3"></em>
                    {{ $t("Clear") }}
                  </a>
                </small>
              </h5>
            </div>
            <div class="listing mt-3">
              <div class="table-responsive">
                <table id="example" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%"
                  aria-describedby="viewlayout">
                  <thead class="thead-bg">
                    <tr>
                      <th scope="col">{{ $t("Name") }}</th>
                      <th scope="col" class="text-center" width="100">{{ $t("Apply") }}</th>
                      <th scope="col" class="text-center" width="100">{{ $t("Edit") }}</th>
                      <th scope="col" class="text-center" width="100">{{ $t("Delete") }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index1) in ListingData" :key="index1">
                      <td>{{ item.VIEW_NAME }}</td>
                      <td class="text-center single-action">
                        <a href="javascript:;" :title="$t('Apply')" v-if="item.IS_SLECTED_USER_ID == 0"
                          v-on:click="ApplyView(item.VIEW_ID, item.VIEW_NAME)">
                          <em class="fa fa-check text-info"></em>
                        </a>
                        <a :title="$t('Applied')" v-if="item.IS_SLECTED_USER_ID > 0">
                          <em class="fa fa-check text-success"></em>
                        </a>
                      </td>
                      <td class="text-center single-action">
                        <a href="javascript:;" :title="$t('Edit')" v-if="item.CAN_EDIT"
                          v-on:click="EditManageView(item.VIEW_ID)">
                          <em class="fa fa-pencil text-success"></em>
                        </a>
                        <a href="javascript:;" title=" $t('Edit') " v-if="!item.CAN_EDIT" class="disabled"
                          style="cursor:no-drop !important;">
                          <em class="fa fa-pencil text-success disabled"></em>
                        </a>
                      </td>
                      <td class="text-center single-action">
                        <a href="javascript:;" :title="$t('Delete')" v-if="item.CAN_EDIT"
                          v-on:click="deleteView(item.VIEW_ID)">
                          <em class="fa fa-trash text-danger"></em>
                        </a>
                        <a href="javascript:;" :title="$t('Delete')" v-if="!item.CAN_EDIT"
                          v-bind:class="{ 'disabled': !item.CAN_EDIT }" v-on:click="deleteView(item.VIEW_ID)">
                          <em class="fa fa-trash text-danger disabled"></em>
                        </a>
                      </td>
                    </tr>
                    <tr v-if="noRecord" style="cursor:initial;">
                      <td colspan="4">
                        <div class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                          {{ $t("NoRecordfound") }}</div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <section class="tab mt-2" v-if="!ShowLisiting">
            <div class="tab-style">
              <div class="tab_container">
                <div class="responsive-tabs-container view_tab">

                  <ul class="nav nav-tabs all-tabs responsive-tabs cust">
                    <li class="nav-item general">
                      <a data-toggle="tab" href="javascript:void(0)" v-on:click="ShowTabData(1)"
                        class="nav-link active">
                        <span class="numb">1</span>
                        {{ $t("General") }}
                      </a>
                    </li>
                    <li class="nav-item selectcolumn">
                      <a data-toggle="tab" href="javascript:void(0)" v-on:click="ShowTabData(2)"
                        v-bind:class="{ 'nav-link': true, 'disabled': (ViewData.Name == '' || (ViewData.ApplyTo == 2 && ListSelectedUsers.length == 0)) }">
                        <span class="numb">2</span>
                        {{ $t("Select Column") }}
                      </a>
                    </li>
                    <li class="nav-item filter">
                      <a data-toggle="tab" href="javascript:void(0)" v-on:click="ShowTabData(3)"
                        v-bind:class="{ 'nav-link': true, 'disabled': ((ViewData.Name == '' || (ViewData.ApplyTo == 2 && ListSelectedUsers.length == 0)) || SelectedItems.length == 0) }">
                        <span class="numb">3</span>
                        {{ $t("Filter") }}
                      </a>
                    </li>
                  </ul>
                  <Form ref="ManageViewLayoutData">
                    <div class="tab-content">
                      <div id="step1" class="tab-pane active">
                        <div class="container-fluid p-0" style="min-height: 416px;">
                          <div class="row">
                            <div class="col-lg-6">
                              <div class="form-group">
                                <label>
                                  {{ $t("ViewName") }}:
                                  <span class="text-danger">*</span>
                                </label>
                                <input v-model.trim="ViewData.Name" class="form-control" />
                              </div>
                            </div>
                            <div class="col-lg-6 float-left">
                              <div class="col-12 float-left p-0">
                                <div class="form-group">
                                  <label>
                                    {{ $t("ApplyTo") }}:
                                    <span class="text-danger">*</span>
                                  </label>
                                  <div class="form-group">
                                    <div class="custom-control custom-radio custom-control-inline">
                                      <input type="radio" class="custom-control-input dynamic" id="customRadio"
                                        v-model="ViewData.ApplyTo" name="viewapplyto" value="1" />
                                      <label class="custom-control-label" for="customRadio">Only For Me</label>
                                    </div>
                      
                                    <div class="custom-control custom-radio custom-control-inline"
                                      v-if="usertype == 'CA'">

                                      <input type="radio" class="custom-control-input dynamic" id="customRadio2"
                                        v-model="ViewData.ApplyTo" name="viewapplyto" value="2" />
                                      <label class="custom-control-label" for="customRadio2">{{ $t("Others") }}</label>
                                    </div>
                                    <div class="custom-control custom-radio custom-control-inline"
                                      v-if="usertype == 'CA'">
                                      <input type="radio" class="custom-control-input dynamic" id="customRadio3"
                                        v-model="ViewData.ApplyTo" name="viewapplyto" value="3" />
                                      <label class="custom-control-label" for="customRadio3">{{ $t("ALL") }}</label>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>


                            <div class="w-100 float-left mb-2 mt-2" v-show="ViewData.ApplyTo == 2">

                              <moveuser v-if="ListUnselectedUsers.length > 0" :leftItems="ListUnselectedUsers"
                                v-bind:right-items="ListSelectedUsers" :title-left="$t('Available')"
                                :title-right="$t('Selectedusers')" moved-item-location="bottom"
                                target-id="ListViewUsers"></moveuser>

                            </div>
                            <div class="col-lg-12 text-right mt-3">
                              <a href="javascript:;" v-on:click="BackToList()" :title="$t('BacktoList')"
                                class="btn btn-secondary mr-1">
                                <em class="fa fa-angle-double-left pr-2"></em>
                                {{ $t("BacktoList") }}
                              </a>
                              <a href="javascript:;" class="btn btn-success mr-1" :title="$t('Next')"
                                v-on:click="ShowNextTab(2)">
                                <em class="fa fa-angle-double-right pr-2"></em>
                                {{ $t("Next") }}
                              </a>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div id="step2" class="tab-pane">
                        <div class="container-fluid p-0" style="min-height: 416px;">
                          <div class="row">
                            <div class="col-12">
                              <mover v-if="UnselectedItems && UnselectedItems.length > 0"
                                v-bind:left-items="UnselectedItems" v-bind:right-items="SelectedItems"
                                :title-left="$t('AvailableFields')" :title-right="$t('SelectedFields')"
                                moved-item-location="bottom" max-item="25" v-cloak>
                              </mover>
                            </div>
                            <div class="col-12 text-right mt-3">
                              <a href="javascript:;" class="btn btn-success" :title="$t('Next')"
                                v-on:click="ShowNextTab(3)">
                                <em class="fa fa-angle-double-right pr-2"></em>
                                {{ $t("Next") }}
                              </a>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div id="step3" class="tab-pane">
                        <div class="container-fluid p-0" style="min-height: 416px;">
                          <div class="container-fluid p-0" style="min-height: 416px;">
                            <div class="row">
                              <div class="col-12">
                                <searchfilter v-bind:submodulecode="SubmoduleCode" v-bind:modulename="ModuleName"
                                  v-bind:viewId="ViewData.ViewId" filter-type="VIEW">
                                  <h5 slot="header">{{ $t("Search") }}</h5>
                                </searchfilter>
                              </div>
                              <div class="col-12 text-right mt-3">
                                <a href="javascript:;" class="btn btn-success mr-1" :title="$t('Save')"
                                  @click.once="SaveViewData()">
                                  <em class="fa fa-save pr-2"></em> {{ $t("Save") }}
                                </a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </Form>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LeadDataService from "../../service/DataService";
import mover from '../Common/mover.vue';
import moveuser from '../Common/moveruser.vue';
import searchfilter from '../Common/layout-filter-control.vue'

export default {
  components: {
    mover,
    moveuser,
    searchfilter
  },
  props: {
    submodulecode: {
      type: String,
      required: true,
    },
    modulename: {
      type: String,
      required: true,
    },
    callbackfunction: {
      type: Function,
    },
    show: {
      type: Boolean,
    },
    usertype: {
      type: String,
    },
  },
  data: function () {
    return {
      isLoading: false,
      StatusId: this.dataselected,
      ViewData: {
        ViewId: 0,
        Name: "",
        Description: "",
        ApplyTo: 1,
        ApplicableUser: "",
        SelectedUser: "",
        Selectoptions: "",
        ViewTabData: [],
        Condition: "",
        FilterData: [],
        DatabaseCondition: "",
      },
      ListUnselectedUsers: [],
      ListSelectedUsers: [],
      ListDepartment: [],
      UnselectedItems: [],
      SelectedItems: [],
      SubmoduleCode: this.submodulecode,
      ModuleName: this.modulename,
      ShowLisiting: true,
      ListingData: [],
      TotalRecords: 0,
      PageNumber: 1,
      PageSize: 10,
      SortBy: "",
      SortExp: "",
      PageSizeOption: null,
      CurrentPage: 1,
      TotalPages: 0,
      GroupStart: 1,
      GroupEnd: 3,
      noRecord: true,
      isClearViewDisabled: true,
      isFilterDataValidationError: false,
      toggleUserGuide: 'd-none'
    };
  },
  created: function () {
    var vm = this;
    vm.GetViewList();
  },
  watch: {
    dataselected: function (value) {
      this.StatusId = value;
    },
  },
  methods: {
    close: function () {
      this.$parent.ShowManageView = false;
      $("#modalManageView").modal("hide");
      $("body").removeClass("modal-open");
      $(".modal-backdrop").remove();
    },
    AddNewView: function () {
      this.ShowLisiting = false;
      this.ViewData.ViewId = 0;
      this.ViewData.Name = "";
      this.FetchData();
    },
    BackToList: function () {
      this.ShowLisiting = true;
    },
    ClearView: function () {
      var vm = this;
      var obj = {
        ModuleName: vm.ModuleName,
        SubmoduleCode: vm.submodulecode,
      };
      var msg =
        vm.$t("Leave_Confirm_Part1") +
        " " +
        vm.$t("Clear") +
        " " +
        vm.$t("View") +
        "?";
      vm.confirmR(msg, true, vm.$t("Confirm"), function () {
        var objectPostString = JSON.stringify(obj);
        LeadDataService.ClearView(objectPostString).then(
          function (response) {
            if (response.data != null) {
              if (response.data.code == "1" || response.body.Code == "1") {
                vm.ShowAlert(vm.$t('ClearedSuccess', [vm.$t('View')]), "Success", true, vm.$t('Alert'));
                vm.close();
                vm.callbackfunction();
              } else {
                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
              }
            } else {
              vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
            }
          }
        );
      })
    },
    DefaultView: function () {
      var vm = this;
      var obj = {
        ModuleName: vm.ModuleName,
        SubmoduleCode: vm.submodulecode,
      };
      var objectPostString = JSON.stringify(obj);
      LeadDataService.ClearView(objectPostString).then(function (response) {
        if (response.data != null) {
          if (response.data.code == "1" || response.data.Code == "1") {
            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("Default View")]), 'success', true, vm.$t('Alert'));

            vm.close();
            vm.callbackfunction();
            vm.GetViewList();
          } else {
            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
          }
        } else {
          vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
        }

      });
      //});
    },
    OnChangeToggle: function (event) {
      debugger;
      var vm = this;
      if (event.target.checked) {
        vm.ViewData.ApplyTo = 1;
      } else {
        vm.ViewData.ApplyTo = 0;
      }
    },
    isBlank: function (str) {
      return !str || /^\s*$/.test(str);
    },
    GetLocalizedValue: function (fieldName, displayName) {
      if (this.isBlank(displayName)) {
        var chk = this.$t(String(fieldName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = $t(String(fieldName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return fieldName;
      } else {
        var chk = this.$t(String(displayName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = this.$t(String(displayName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return displayName;
      }
    },
    CreateString: function (arr, key, type) {
      return arr
        .map(function (obj) {
          if (type == "int") {
            return obj[key];
          } else {
            return "'" + obj[key] + "'";
          }
        })
        .join(",");
    },
    GetViewList: function () {
      var vm = this;
      var ListApiURL =
        "moduleName=" +
        vm.modulename +
        "&subModuleCode=" +
        vm.submodulecode +
        "&viewId=" +
        vm.ViewData.ViewId +
        "&pageSize=" +
        this.PageSize +
        "&pageNum=" +
        this.PageNumber +
        "&sortBy=" +
        this.SortBy +
        "&sortExp=" +
        this.SortExp;
      //  debugger
      LeadDataService.GetViewList(ListApiURL).then(
        function (response) {
          if (response.data != null && response.data != "") {
            vm.ListingData = response.data.DATA;
            vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS;
            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
            vm.CurrentPage = vm.PageNumber;
            vm.noRecord = false;
            var chkVACTIVE = 0;
            vm.ListingData.forEach(function (item) {
              if (item.IS_SLECTED_USER_ID > 0) {
                chkVACTIVE = chkVACTIVE + 1;
              }
            });
            var indx = 0;
            if (chkVACTIVE > 0) {
              vm.ListingData.splice(indx, 0, {
                SUB_MODULE_NAME: "",
                SUB_MODULE_CODE: "",
                VIEW_NAME: "Default View",
                VIEW_ID: 0,
                IS_APPLICABLE_TO_ALL: 1,
                MODULE_NAME: "",
                CREATED_AT: "",
                IS_SLECTED_USER_ID: 0,
                CAN_EDIT: 0,
                ROWNUMBER: parseInt(vm.TotalRecords + 1),
                VIEW_TYPE: "",
                USER_LIST: "",
              });
            } else {
              vm.ListingData.splice(indx, 0, {
                SUB_MODULE_NAME: "",
                SUB_MODULE_CODE: "",
                VIEW_NAME: "Default View",
                VIEW_ID: 0,
                IS_APPLICABLE_TO_ALL: 1,
                MODULE_NAME: "",
                CREATED_AT: "",
                IS_SLECTED_USER_ID: 1,
                CAN_EDIT: 0,
                ROWNUMBER: parseInt(vm.TotalRecords + 1),
                VIEW_TYPE: "",
                USER_LIST: "",
              });
            }

          } else {

            vm.noRecord = false;
            vm.TotalRecords = 1;
            vm.ListingData.push({
              SUB_MODULE_NAME: "",
              SUB_MODULE_CODE: "",
              VIEW_NAME: "Default View",
              VIEW_ID: 0,
              IS_APPLICABLE_TO_ALL: 1,
              MODULE_NAME: "",
              CREATED_AT: "",
              IS_SLECTED_USER_ID: 0,
              CAN_EDIT: 0,
              ROWNUMBER: vm.TotalRecords,
              VIEW_TYPE: "",
              USER_LIST: "",
            });
          }
          setTimeout(function () { vm.SetBootstraptoolTip() }, 200);
          vm.checkIfClearViewDisabled();
        }
      );
    },
    deleteView: function (val) {
      var vm = this;
      vm.confirmR(vm.$t("ConfirmDelete"), true, vm.$t('Remove'), function () {
        var postJSON = {
          ids: val,
          moduleName: "",
          subModuleCode: "",
          refCode: 'DELETE_HOME_MANAGE_VIEW'
        };
        vm.isLoading = true;
        var objectPostString = JSON.stringify(postJSON);
        LeadDataService.CommonCommonDelete(objectPostString).then(function (response) {
          vm.isLoading = false;
          var box = "";
          if (response.data[0].Status == "Success") {

            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('View')]), 'success', true, vm.$t('Alert'));
            vm.callbackfunction();

            $("#modalManageView").modal('hide');
            window.location.reload();
          }
          else {

            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('View')]), "warning", true, vm.$t("Alert"));
          }
        });
      });
    },
    EditManageView: function (id) {
      debugger
      var vm = this;
      vm.ViewData.ViewId = id;
      var url =
        "moduleName=" +
        vm.modulename +
        "&subModuleCode=" +
        vm.submodulecode +
        "&id=" +
        vm.ViewData.ViewId;
      LeadDataService.GetViewById(url).then(
        function (response) {
          if (response.data != null) {
            var data = response.data.DATA;
            debugger
            if (data.length > 0) {
              data = data[0];
              vm.ViewData.Name = data.VIEW_NAME;
              vm.ViewData.Description = data.DESCRIPTION;
              vm.ViewData.ApplyTo = data.IS_APPLICABLE_TO_ALL;
              vm.ViewData.Condition = data.CONDITION;
              vm.ViewData.DatabaseCondition = encodeURIComponent(
                data.DATABASE_CONDITION
              );
            }
            vm.ShowLisiting = false;
            vm.FetchData();
          }
        }
      );
    },
    ApplyView: function (id, name) {
      var vm = this;
      if (id == 0) {
        vm.DefaultView();
      } else {
        var obj = {
          ViewId: id,
          ModuleName: vm.ModuleName,
          SubmoduleCode: vm.submodulecode,
        };
        var objectPostString = JSON.stringify(obj);
        LeadDataService.ApplyView(objectPostString).then(function (response) {

          if (response.data != null) {
            if (response.data.code == "1" || response.data.Code == "1") {
              vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t(name)]), 'success', true, vm.$t('Alert'));
              vm.close();
              vm.callbackfunction();
              vm.GetViewList();
            } else {
              vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
            }
          } else {
            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
          }

        });
      }
    },
    SaveViewData: function () {
      debugger
      var vm = this;
      var msg = "";
      $("#spnlistfieldssmessage").hide();
      $("#spnusersmessage").hide();
      $("#spncardfieldssmessage").hide();
      $("#spncardtabsmessage").hide();
      if (vm.SelectedItems == null || vm.SelectedItems.length == 0) {
        $("#spnlistfieldssmessage").show();
        msg += "no";
      }
      if (
        vm.ViewData.IsApplicableToAll == "0" &&
        (vm.ListSelectedUsers == null || vm.ListSelectedUsers.length == 0)
      ) {
        $("#spnusersmessage").show();
        msg += "no";
      }
      vm.$refs.ManageViewLayoutData.validate().then((valid) => {
        if (valid
        ) {
          var listItems = [];
          $.each(vm.SelectedItems, function (index, item) {
            var obj = {
              id: item.value,
              order: index + 1,
              isSortable: item.isSortable,
            };
            listItems.push(obj);
          });

          vm.ViewData.DatabaseCondition = decodeURIComponent(
            vm.ViewData.DatabaseCondition
          );
          var obj = {
            ViewData: vm.ViewData,
            ListFields: listItems,
            FilterData: vm.ViewData.FilterData,
            ShareUsers: vm.CreateString(vm.ListSelectedUsers, "value", "int"),
            ModuleName: vm.ModuleName,
            SubmoduleCode: vm.submodulecode,
          };
          // debugger 
          var objectPostString = JSON.stringify(obj);
          LeadDataService.SaveView(objectPostString).then(function (response) {
            //debugger
            if (response.data != null) {
              if (response.data.code == "1" || response.data.Code == "1") {
                if (vm.ViewData.ViewId > 0) {
                  vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("View")]), "success", true, vm.$t("Alert"));
                  vm.callbackfunction();
                } else {
                  vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("View")]), "success", true, vm.$t("Alert"));
                  vm.callbackfunction();
                }
                vm.GetViewList();
                vm.ShowLisiting = true;
                vm.close();
              } else {
                vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), "failure", true, vm.$t("Alert"));
              }
            } else {
              vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), "failure", true, vm.$t("Alert"));
            }
          }
          );
        } else {
          console.log(this.$refs.ManageViewLayoutData.errors);
        }
      });
    },
    FetchData: function () {
      debugger
      var vm = this;
      vm.isLoading = true;
      var ListApiURL =
        "moduleName=" +
        vm.modulename +
        "&subModuleCode=" +
        vm.submodulecode +
        "&viewId=" +
        vm.ViewData.ViewId;
      vm.UnselectedItems = [];
      vm.SelectedItems = [];
      LeadDataService.ViewListFields(ListApiURL).then(
        function (response) {
          debugger
          if (response.data.AVAILABLE_FIELDS.length !== response.data.SELECTED_FIELDS.length) {
            //debugger
            for (var i = response.data.AVAILABLE_FIELDS.length - 1; i >= 0; i--) {
              for (var j = 0; j < response.data.SELECTED_FIELDS.length; j++) {
                if (
                  response.data.AVAILABLE_FIELDS[i] &&
                  response.data.AVAILABLE_FIELDS[i].NAME ===
                  response.data.SELECTED_FIELDS[j].NAME
                ) {
                  response.data.AVAILABLE_FIELDS.splice(i, 1);
                }
              }
            }
          }
          if (response.data.AVAILABLE_FIELDS.length > 0) {
            $.each(response.data.AVAILABLE_FIELDS, function (key, item) {
              var obj = {
                value: item.CUSTOM_FIELD_ID,
                displayValue: item.DISPLAY_NAME,
                isSortable: item.IS_SORTABLE,
                displayOrder: item.DISPLAY_ORDER,
                isSelected: false,
                isSystemGenerated: item.IS_SYSTEM_GENERATED,
                isSortableEnabled: item.IS_SORTABLE_ENABLE
              };
              vm.UnselectedItems.push(obj);
            });
          }

          $.each(response.data.SELECTED_FIELDS, function (key, item) {
            var obj = {
              value: item.CUSTOM_FIELD_ID,
              displayValue: item.DISPLAY_NAME,
              isSortable: item.IS_SORTABLE,
              displayOrder: item.DISPLAY_ORDER,
              isSelected: false,
              isSystemGenerated: item.IS_SYSTEM_GENERATED,
              isSortableEnabled: item.IS_SORTABLE_ENABLE
            };
            vm.SelectedItems.push(obj);
          });
          debugger
          vm.isLoading = false;
          vm.FetchUserData();
        });
    },
    FetchUserData: function () {
      debugger
      var vm = this;
      vm.isLoading = true;
      vm.ListUnselectedUsers = [];
      vm.ListSelectedUsers = [];
      var ListApiURLUser = "moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode + "&viewId=" + vm.ViewData.ViewId;
      LeadDataService.ViewListUsers(ListApiURLUser)
        .then(function (response) {
          vm.isLoading = false;
          var newList = [];
          $.each(response.data.AVAILABLE_FIELDS, function (key, item) {
            var obj = {
              value: item.USER_ID,
              displayValue: item.USER_NAME,
              departmentName: item.DEPARTMENT_NAME,
              isSelected: false
            };
            vm.ListUnselectedUsers.push(obj);
          });

          // vm.ListUnselectedUsers = newList;
          newList = [];

          $.each(response.data.SELECTED_FIELDS, function (key, item) {
            var obj = {
              value: item.USER_ID,
              displayValue: item.USER_NAME,
              departmentName: item.DEPARTMENT_NAME,
              isSelected: false
            };
            vm.ListSelectedUsers.push(obj);

          });

        })

    },
    ShowNextTab: function (type) {
      var vm = this;
      if (type == 2) {
        $("#scrollbarreplyuserguideMangeView .con").html(vm.$t("MangeViewUG2"));
        if ($.trim(vm.ViewData.Name) == "") {
          vm.ShowAlert(vm.$t('Required', [vm.$t('ViewName')]), 'warning', true, vm.$t('Alert'));
        } else if (vm.ViewData.ApplyTo == 2 && vm.ListSelectedUsers.length == 0) {
          vm.ShowAlert(vm.$t('PleaseSelectAtleastOneUser'), 'warning', true, vm.$t('Alert'));
        } else {
          $(".view_tab a").removeClass('active show');
          $('li.selectcolumn').find('a').addClass('active');
          $(".view_tab .tab-pane").removeClass('active show');
          $(".view_tab a[href='#step2']").addClass('active show');
          $(".view_tab #step2").addClass('active show');
        }
      }
      else if (type == 3) {
        $("#scrollbarreplyuserguideMangeView .con").html(vm.$t("MangeViewUG3"));
        var maxlength = 25;
        if ($.trim(vm.ViewData.Name) == "") {
          vm.ShowAlert(vm.$t('Required', [vm.$t('ViewName')]), true, 'warning', vm.$t('Alert'));
        } else if (vm.ViewData.ApplyTo == 2 && vm.ListSelectedUsers.length == 0) {
          vm.ShowAlert(vm.$t('PleaseSelectAtleastOneUser'));
        } else if (vm.SelectedItems.length == 0) {
          vm.ShowAlert(vm.$t('Pleaseselectatleastonecolumn'), 'warning', true, vm.$t('Alert'));
        }
        else if (vm.SelectedItems.length > maxlength) {
          vm.ShowAlert(vm.$t('SelectFieldMessage', [maxlength]), 'warning', true, vm.$t('Alert'));
        } else {
          $(".view_tab a").removeClass('active show');
          $('li.filter').find('a').addClass('active');
          $(".view_tab .tab-pane").removeClass('active show');
          $(".view_tab a[href='#step3']").addClass('active show');
          $(".view_tab #step3").addClass('active show');
        }
      }
    },
    checkIfClearViewDisabled: function () {
      var vm = this;
      if (vm.ListingData != null && vm.ListingData.length > 0) {
        var checkIfAnyViewApplied = vm.ListingData.filter(function (x) { return x.IS_SLECTED_USER_ID > 0 });
        if (checkIfAnyViewApplied.length > 0) {
          vm.isClearViewDisabled = false;
        } else {
          vm.isClearViewDisabled = true;
        }
      } else {
        vm.isClearViewDisabled = true;
      }
    },
    ShowTabData: function (type) {
      if (type == '1') {
        $("#scrollbarreplyuserguideMangeView .con").html(this.$t("MangeViewUG1"));
        $('li.general').find('a').addClass('active');
        $('li.selectcolumn').find('a').removeClass('active');
        $('li.filter').find('a').removeClass('active');

        $('div#step1').addClass('active show');
        $('div#step2').removeClass('active show');
        $('div#step3').removeClass('active show');
      }
      else if (type == '2') {
        $("#scrollbarreplyuserguideMangeView .con").html(this.$t("MangeViewUG2"));
        $('li.general').find('a').removeClass('active');
        $('li.selectcolumn').find('a').addClass('active');
        $('li.filter').find('a').removeClass('active');

        $('div#step1').removeClass('active show');
        $('div#step2').addClass('active show');
        $('div#step3').removeClass('active show');
      }
      else if (type == '3') {
        $("#scrollbarreplyuserguideMangeView .con").html(this.$t("MangeViewUG3"));
        $('li.general').find('a').removeClass('active');
        $('li.selectcolumn').find('a').removeClass('active');
        $('li.filter').find('a').addClass('active');

        $('div#step1').removeClass('active show');
        $('div#step2').removeClass('active show');
        $('div#step3').addClass('active show');
      }

      this.$forceUpdate();
    },
    ShowUserGuide() {
      var vm = this;
      if (vm.toggleUserGuide == 'd-none') {
        vm.toggleUserGuide = 'd-block';
      }
    }
  }
}
</script>